<template>
  <div id="app">
    <vs-input vs-label-placeholder="Usuario" v-model="value1"/>

    <vs-input vs-label-placeholder="Contraseña" v-model="value1"/>

    <vs-button vs-color="primary" vs-type="filled">Primary</vs-button>
    <vs-button vs-color="success" vs-type="filled">Success</vs-button>
    <vs-button vs-color="danger" vs-type="filled">Danger</vs-button>
    <vs-button vs-color="warning" vs-type="filled">Warning</vs-button>
    <vs-button vs-color="dark" vs-type="filled">Dark</vs-button>
    <vs-button vs-color="rgb(134, 4, 98)" vs-type="filled">RGB</vs-button>
  </div>
</template>

<script>
export default {
  name: 'Panel',
  data () {
    return {
      value1: '',
      value2: ''
    }
  },
  computed: {
    cards () {
      return this.$store.state.cards
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .card-item {
    width: 120px;
    display: inline-block;
    margin: 5px;
  }
  .card .card-content {
    padding: 0.5rem;
  }
  .card .card-content .media-content {
    overflow: initial;
  }
  .card .card-content .title {
    margin-bottom: 5px;
  }
  .card .card-content .media-content .data-card {
    font-size: 11px;
  }
  .card .card-content .media-content .data-card div {
    display: inline-block;
  }
  .card .card-content .media-content .data-card .card-flag img {
    width: 20px;
  }
  button.delete {
    position: absolute;
    top: 5px;
    right: 5px;
  }
</style>



