<template>
  <div class="columns">
    <div class="column column is-half is-offset-one-quarter panel panel-login">
      <div class="columns">
        <div class="column">
          <h4 class="title is-4">TP-Beat</h4>
          <ul>
            <li>Crea tu Grupo</li>
            <li>Invita a tus amigos</li>
            <li>Predice tu score, apuesta y Gana!</li>
          </ul>
          <vs-divider vs-position="center">Inciar Sesión</vs-divider>
          <vs-button vs-color="primary" vs-type="filled" @click="authenticate('facebook')"><span class="icon"><i class="fa fa-facebook"></i></span>
            Iniciar Sesión con FB</vs-button>
        </div>
        <div class="column">
          <div class="columns">
            <div class="column">
              <img width="180" src="@/assets/img/rusia2018.png" alt="Placeholder image">
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Login',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      user: '',
      password: ''
    }
  },
  methods: {
    authenticate: function (provider) {
      this.$auth.authenticate(provider).then(function () {
        console.log(provider)
        // Execute application logic after successful social authentication
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.panel{
  margin-bottom: 22px;
  background-color: #fff;
  border: 1px solid transparent;
  border-radius: 4px;
  box-shadow: 0px 1px 3px 0px rgba(0,0,0,0.2), 0px 1px 1px 0px rgba(0,0,0,0.14), 0px 2px 1px -1px rgba(0,0,0,0.12);
}
.panel-login{
  margin-top: 10%;
  padding: 25px 15px;
}
</style>



